/*
@family: имя шрифта (обязательно).
@filename: название шрифта, так же используется в качестве id для шрифта в формате svg (обязательно, по умолчанию используется имя шрифта).
@weight: жирность шрифта (не обязательно, по умолчанию normal).
@style: стиль шрифта (не обязательно, по умолчанию normal).

Пример использования:

.font-face(Roboto Slab, RobotoSlab-Bold, bold);
*/

/* 
html{
    background-image: url( '../img/PixelPerfect.jpg' );
    background-position: 0 0;
    background-repeat: no-repeat;
    opacity: 0.5;
    z-index: 0;
}
 */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  z-index: 1;
  max-width: 1280px;
  margin: 0 auto;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.product-header {
  width: 100%;
  min-height: 323px;
  background-color: #445161;
}

.product-header .product-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 40px;
}

.product-header .product-wrapper-header {
  width: 50%;
}

.product-header .product-wrapper-header p {
  padding-left: 5px;
  color: #ffffff;
  font-family: "Arial MT", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
}

.product-header .product-wrapper-header p .fa-check {
  font-size: 25px;
  padding-right: 5px;
}

.product-header .product-wrapper-header-name {
  color: #ffffff;
  font-family: "Arial MT", sans-serif;
  font-size: 60px;
  font-weight: 400;
  margin-bottom: 25px;
}

.product-header .product-wrapper-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  width: 50%;
}

.product-header .product-wrapper-block .block {
  width: 469px;
  height: 214px;
  background-color: #fff;
}

.Reviews {
  background-color: #f5f5f5;
  padding-bottom: 35px;
}

.Reviews .Reviews-wrapper {
  padding-top: 35px;
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}

.Reviews .Reviews-wrapper h2 {
  text-align: center;
  color: #445161;
  font-family: "Arial MT", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 36px;
}

.Reviews .Reviews-wrapper-items {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.Reviews .Reviews-wrapper-items .item {
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 50%;
}

.Reviews .Reviews-wrapper-items .item-img {
  width: 75px;
  height: 75px;
  border-radius: 100%;
  background-color: #445161;
}

.Reviews .Reviews-wrapper-items .item-review {
  margin-left: 16px;
  width: 81%;
  background-color: #ebebeb;
  position: relative;
  padding-bottom: 20px;
  border-radius: 5px;
}

.Reviews .Reviews-wrapper-items .item-review::after {
  content: '';
  position: absolute;
  left: -25px;
  top: 25px;
  border: 10px solid transparent;
  border-right: 15px solid #ebebeb;
}

.Reviews .Reviews-wrapper-items .item-review p {
  color: #333333;
  font-family: "Arial - Italic MT", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  padding: 16px 22px 15px 27px;
}

.Reviews .Reviews-wrapper-items .item-review span {
  color: #989898;
  font-family: "Arial MT", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  padding: 0 0 16px 27px;
}

.buy {
  padding-bottom: 35px;
}

.buy .buy-wrapper {
  padding-top: 35px;
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}

.buy .buy-wrapper h2 {
  color: #445161;
  font-family: "Arial MT", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 36px;
  text-align: center;
}

.buy .buy-wrapper .buy-items {
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.buy .buy-wrapper .buy-items .item {
  margin-left: 45px;
  background-color: #445161;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-bottom: 20px;
  border-radius: 5px;
}

.buy .buy-wrapper .buy-items .item h3 {
  color: #ffffff;
  font-family: "Arial MT", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  margin-top: 16px;
  margin-bottom: 17px;
}

.buy .buy-wrapper .buy-items .item .price {
  align-self: center;
  width: 98%;
  min-height: 70px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  margin-bottom: 22px;
}

.buy .buy-wrapper .buy-items .item .price span {
  color: #445162;
  font-family: "Arial MT", sans-serif;
  font-size: 40px;
  font-weight: 400;
  line-height: 30px;
}

.buy .buy-wrapper .buy-items .item p {
  color: #ffffff;
  font-family: "Arial MT", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  padding: 0 38px 0 20px;
}

.buy .buy-wrapper .buy-items .item .buy-button {
  align-self: center;
  margin-top: 22px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 14px 111px;
  color: #445161;
  font-family: "Arial - Bold MT", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #fff;
}

.contacts {
  margin-top: 100px;
  position: relative;
  width: 100%;
  background: #445161;
  padding-bottom: 50px;
}

.contacts:before {
  content: '';
  position: absolute;
  top: -50px;
  width: 0;
  height: 0;
  border-bottom: 50px solid #445161;
  border-right: 94vw solid transparent;
}

.contacts-wrapper {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}

.contacts-wrapper h2 {
  color: #ffffff;
  font-family: "Arial MT", sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 36px;
  text-align: center;
}

.contacts .contacts-contact-form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.contacts .contacts-contact-form form fieldset {
  display: flex;
}

.contacts .contacts-contact-form form fieldset input,
.contacts .contacts-contact-form form fieldset textarea {
  width: 85%;
  margin-bottom: 12px;
  color: #445162;
  font-family: "Arial MT", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  box-sizing: border-box;
  padding-left: 10px;
  border-radius: 5px;
  border: none;
}

.contacts .contacts-contact-form form fieldset .buttom {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 85%;
}

.contacts .contacts-contact-form form fieldset .buttom .submit {
  color: #445162;
  font-family: "Arial - Bold MT", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 10px 48px;
  border: none;
  border-radius: 5px;
  margin-top: -20px;
  background-color: #fff;
}

.contacts .contacts-contact-form .contact {
  align-self: flex-start;
}

.contacts .contacts-contact-form .contact p {
  color: #ffffff;
  font-family: "Arial MT", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  padding-bottom: 15px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
}

.contacts .contacts-contact-form .contact p img {
  padding-right: 5px;
}

.contacts .contacts-contact-form .contact .social {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 15px;
}

.contacts .contacts-contact-form .contact .social div {
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: #fff;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
}

.contacts .contacts-contact-form .contact .social div .fa {
  color: #445161;
  cursor: pointer;
}

@media screen and (max-width: 1200px) {
  .product-header .product-wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .Reviews .Reviews-wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .buy .buy-wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .contacts-wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .product-header .product-wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .product-header .product-wrapper-header p {
    font-size: 16px;
  }

  .Reviews .Reviews-wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .Reviews .Reviews-wrapper-items .item-review {
    width: 75%;
  }

  .buy .buy-wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .buy .buy-wrapper .buy-items {
    flex-direction: column;
    align-items: center;
  }

  .buy .buy-wrapper .buy-items .item {
    margin-top: 15px;
  }

  .contacts-wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 800px) {
  .product-header .product-wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .product-header .product-wrapper-header {
    width: 100%;
  }

  .product-header .product-wrapper-block {
    display: none;
  }

  .Reviews .Reviews-wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .Reviews .Reviews-wrapper-items .item {
    width: 100%;
  }

  .buy .buy-wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .contacts-wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .product-header .product-wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .Reviews .Reviews-wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .buy .buy-wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .contacts-wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .contacts .contacts-contact-form {
    flex-direction: column;
  }

  .contacts .contacts-contact-form .contact {
    margin: 0 auto;
    margin-top: 30px;
  }
}